import * as React from 'react';

import * as C from './styled';

import { SkeletonLoader } from '~/components';

export default function List() {
    return (
        <C.Container>
            {Array(7)
                .fill(0)
                .map((i) => (
                    <C.ContainerItemList key={i}>
                        <SkeletonLoader />
                    </C.ContainerItemList>
                ))}
        </C.Container>
    );
}
