import * as C from './styled';

export interface ISkeletonLoader {
    height?: string;
    width?: string;
    radius?: string;
    margin?: string;
}

export default function SkeletonLoader(props: ISkeletonLoader) {
    return <C.Skeleton {...props} />;
}
